.map {
    height: 500px;
    background-color: white;
    padding: 1rem;
    border-radius: 20px;
    margin-top: 16px;
    box-shadow: 0 0 8px -4px rgba(0,0, 0,0.5);

}

.map .leaflet-container{
    height: 100%;
}
.infoFlag {
    height: 80px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
}
.infoFlag img {
    width:100px;
    border-radius: 5px;
}